<template>
<v-container>

<v-toolbar flat color="white">
<v-toolbar-title>Crud Atma pedia</v-toolbar-title>
<v-divider class="mx-4" inset vertical></v-divider>
<!-- <v-spacer></v-spacer> -->
<v-dialog v-model="dialog" max-width="500px">
<template v-slot:activator="{ on }">
<v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
</template>
<v-card>
<v-card-title>
<span class="headline">{{ formTitle }}</span>
</v-card-title>
<v-card-text>
<v-container>
<v-row>
<v-col cols="12" sm="6" md="4">
<v-text-field v-model="editedItem.name" label="Drink name"></v-text-field>
</v-col>
<v-col cols="12" sm="6" md="4">
<v-select
v-model="editedItem.type"
:items="items"
label="Jenis"
></v-select>
</v-col>
<v-col cols="12" sm="6" md="4">
<v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
</v-col>
<v-col cols="12" sm="6" md="4">
<v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
</v-col>
<v-col cols="12" sm="6" md="4">
<v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
</v-col>
</v-row>
</v-container>
</v-card-text>
<v-card-actions>
<v-spacer></v-spacer>
<v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
<v-btn color="blue darken-1" text @click="save">Save</v-btn>
</v-card-actions>
</v-card>
</v-dialog>
</v-toolbar>

<v-row>
<v-col cols="3" v-for="drink in drinks" :key="drink.name">
<v-card
max-width="344"
outlined
class="d-inline-flex" >
<v-list-item three-line>
<v-list-item-content>
<div class="overline mb-4">MINUMAN</div>
<v-list-item-title class="headline mb-1">{{drink.name}}</v-list-item-title>
<v-list-item-subtitle>Type: {{drink.type}}</v-list-item-subtitle>
<v-list-item-subtitle>Fat: {{drink.fat}}</v-list-item-subtitle>
<v-list-item-subtitle>Carbs: {{drink.carbs}}</v-list-item-subtitle>
<v-list-item-subtitle>Protein: {{drink.protein}}</v-list-item-subtitle>
</v-list-item-content>
</v-list-item>

<v-card-actions>
<v-icon class="mr-1" color="teal" @click="editItem(drink)">mdi-silverware</v-icon>
<v-icon class="mr-1" color="red"  @click="deleteItem(drink)">mdi-delete</v-icon>
</v-card-actions>
</v-card>
</v-col>
</v-row>
</v-container>
</template>

<script src="https://cdnjs.cloudflare.com/ajax/libs/vue/1.0.26/vue.js"></script>

<script>

import{minumanRef} from '../firebase';

export default {
data: () => ({
cek: -1,
dialog: false,
checkDelete:[],
search: "",
items: [
'Es',
'Panas',
],
headers: [
{
text: "Drink (100g serving)",
align: "left",
sortable: false,
value: "name"
},
{ text: "Type", value: "type" },
{ text: "Fat (g)", value: "fat" },
{ text: "Carbs (g)", value: "carbs" },
{ text: "Protein (g)", value: "protein" },
{ text: "Actions", value: "action", sortable: false }
],
drinks: [],
editedIndex: -1,
editedItem: {
name: "",
type: "",
fat: 0,
carbs: 0,
protein: 0
},
defaultItem: {
name: "",
type: "",
fat: 0,
carbs: 0,
protein: 0
}
}),

firebase: {
drinks :minumanRef
},

computed: {
formTitle() {
return this.cek === -1 ? "New Item" : "Edit Item";
}
},
watch: {
dialog(val) {
val || this.close();
}
},
methods: {
editItem(item) {
this.editedIndex = item['.key']
this.cek = 0
this.editedItem = Object.assign({}, item)
this.dialog = true
},
deleteItem(item) {
confirm('Are you sure you want to delete this item?') && minumanRef.child((item['.key'])).remove()
},
multipleDelete(){
for(var i=0;i<this.checkDelete.length;i++){
minumanRef.child(this.checkDelete[i]).remove()
}
this.checkDelete=[]
},
close() {
this.dialog = false;
setTimeout(() => {
this.editedItem = Object.assign({}, this.defaultItem)
this.editedIndex = -1
}, 300)
},
save() {
if (this.cek > -1) {
minumanRef.child(this.editedIndex).set({name: this.editedItem.name,type: this.editedItem.type, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})
this.clear()
} else {
minumanRef.push({name: this.editedItem.name,type: this.editedItem.type, fat: this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein});
this.clear()
}
this.close();
},
clear(){
this.editedItem={}
this.editedItem.name= ''
this.editedItem.type= ''
this.editedItem.fat= 0
this.editedItem.carbs= 0
this.editedItem.protein= 0
this.cek=-1
}
}
};
</script>
